.header {
    display: block;
    margin: 0 auto;
    width: 100%;
    /*max-width: 100%;*/
    box-shadow: none;
    background-color: rgb(74, 161, 235);
    position: fixed;
    height: 50px;
    z-index: 10;
    text-align: center;
}

input[type="checkbox"]:checked ~ #sidebarMenu {
    transform: translateX(0);
}

input[type=checkbox] {
    transition: all 0.3s;
    box-sizing: border-box;
    display: none;
}

.sidebarIconToggle {
    transition: all 0.3s;
    box-sizing: border-box;
    cursor: pointer;
    position: absolute;
    z-index: 99;
    top: 17.5px;
    left: 15px;
    width: 22px;
}

.spinner {
    transition: all 0.3s;
    box-sizing: border-box;
    position: absolute;
    height: 3px;
    width: 100%;
    background-color: #fff;
}

.line-1 {
    transition: all 0.3s;
    box-sizing: border-box;
    position: relative;
    float: left;
}

.line-2 {
    transition: all 0.3s;
    box-sizing: border-box;
    position: relative;
    float: left;
    margin-top: 3px;
}

.line-3 {
    transition: all 0.3s;
    box-sizing: border-box;
    position: relative;
    float: left;
    margin-top: 3px;
}

.header img {
    width: 50px;
    height: 50px;
}

input[type=checkbox]:checked ~ .sidebarIconToggle > .line-2 {
    transition: all 0.3s;
    box-sizing: border-box;
    opacity: 0;
}

input[type=checkbox]:checked ~ .sidebarIconToggle > .line-1 {
    transition: all 0.3s;
    box-sizing: border-box;
    transform: rotate(135deg);
    margin-top: 6px;
}

input[type=checkbox]:checked ~ .sidebarIconToggle > .line-3 {
    transition: all 0.3s;
    box-sizing: border-box;
    transform: rotate(-135deg);
    margin-top: -9px;
}

#sidebarMenu {
    height: 100%;
    position: fixed;
    left: 0; /* position after transition */
    top: 50px; /* position after transition */
    max-width: 200px;
    width: 40vw;
    transform: translateX(-500px); /* position before transition */
    transition: transform 250ms ease-in-out;
    border-right: 1px solid rgb(236, 238, 240);
    border-left: 1px solid rgb(236, 238, 240);
    background-color: #fff;
}

.sidebarMenuInner{
    margin:0;
    padding:0;
}

.sidebarMenuInner li{
    list-style: none;
    padding: 1rem;
    border-bottom: 1px solid rgb(236, 238, 240);
}

.sidebarMenuInner a{
    cursor: pointer;
    text-decoration: none;
}

.sidebarMenuInner a.active h2 {
    color: rgb(74, 161, 235);
}

.sidebarMenuInner h2 {
    color: #000;
    font-size: 1.2rem;
    margin: 0;
}

.sidebarMenuInner h2:hover {
    color: rgb(74, 161, 235);
}

@media (min-width: 601px) {
    .header {
        width: 85%;
    }

    #sidebarMenu {
        left: 7.5%;
    }
}

@media (min-width: 769px) {
    .header {
        width: 70%;
    }

    #sidebarMenu {
        left: 15%;
    }
}


