.wrapper {
	display: flex;
	align-items: center;
}

.wrapper img {
	width: 1.5rem;
	height: 1.5rem;
	vertical-align: middle;
}

.wrapper span {
	vertical-align: middle;
	margin-left: 1rem;
}

span.edit-icon-wrapper {
	display: inline-block;
	padding: .5rem;
	margin-left: auto;
	height: 36.8px;
}

span.edit-icon-wrapper:hover {
	background-color: rgb(237, 245, 253);
	border-radius: 50%;
}

.wrapper button {
	float: right;
	margin-left: 1rem;
	margin-top: .6rem;
	padding: .6rem;
	border: none;
	border-radius: 5px;
	font-size: .9rem;
	font-weight: 500;
	cursor: pointer;
}

.wrapper #cancel {
	background-color: rgb(228, 230, 235);
}

.wrapper #cancel:hover {
	background-color: rgb(216, 218, 223);
}

.wrapper #save {
	background-color: rgb(53, 120, 234);
	color: #ffffff;
}

.wrapper #save:hover {
	background-color: rgb(50, 114, 222);
}

.wrapper p {
	display: inline-block;
	white-space: pre-line;
	margin: 0 0 0 1rem;
}

.wrapper img[alt="edit"] {
	width: 1.3rem;
	height: 1.3rem;
	cursor: pointer;
}

.wrapper select {
	font-size: 1rem;
	/*vertical-align: middle;*/
	width: 100%;
	height: 3rem;
	color: dimgray;
	border-radius: 4px;
	border: 1px solid rgba(0, 0, 0, 0.23);
	-webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    padding: 0 1rem;
}

.wrapper option {
	padding: 0;
}

.wrapper textarea {
	font-size: 1rem;
	font-family: 'Roboto', -apple-system, sans-serif;
	width: 100%;
	height: 5rem;
	padding: 1rem;
	border-radius: 4px;
	border: 1px solid rgba(0, 0, 0, 0.23);
	resize: none;
	overflow: auto;
}

.wrapper select:hover,
.wrapper textarea:hover {
	border-color: rgba(0, 0, 0, 0.87);
}

.wrapper select:active,
.wrapper textarea:active {
	border-color: rgb(53, 118, 203);
}

.initial-wrapper {
	cursor: pointer;
	display: flex;
	align-items: center;
	height: 36.8px;
}

.initial-wrapper span {
	color: #4AA1EB;
}

.edit-section-wrapper {
	width: 100%;
}
