.cover-photo-wrapper {
	width: 100%; 
	max-height: 200px; 
	height: 200px;
	background-color: #cccccc;
	background-size: cover;
	background-position: center;
  background-repeat: no-repeat;
	position: relative;
}

.cover-photo-wrapper img.avatar {
	position: absolute;
	margin: 0;
	left: calc(50% - 3.5rem);
	bottom: -1.5rem;
	width: 7rem;
	height: 7rem;
	border-radius: 50%;
	border: 4px solid #ffffff;
}

.cover-photo-wrapper input {
	display: none;
}

.cover-photo-wrapper button {
	position: absolute;
	right: 1rem;
	bottom: 1rem;
	padding: .5rem;
	border-radius: 4px;
	border: none;
	/*background-color: rgb(53, 118, 203);*/
	cursor: pointer;
}

.cover-photo-wrapper button:hover {
	background-color: rgb(216, 218, 223);
}

.cover-photo-wrapper button:focus {
	outline: none;
}

.cover-photo-wrapper button img {
	width: 1rem;
	height: 1rem;
	vertical-align: middle;
}

.cover-photo-wrapper button span {
	vertical-align: middle;
	margin-left: .5rem;
}

@media (max-width: 992px) {
	.cover-photo-wrapper {
		margin-top: 50px;
	}
}

@media (max-width: 600px) {
	.cover-photo-wrapper img.avatar {
		width: 6rem;
		height: 6rem;
		left: calc(50% - 3rem);
	}

	.cover-photo-wrapper button {
		top: 1rem;
		bottom: auto;
	}
}

@media (max-width: 400px) {
	.cover-photo-wrapper {
		height: 50vw;
	}
}







