.tweets-area {
	margin-top: 1.5rem;
	border-top: 1px solid rgb(236, 238, 240);
	display: flex;
}

.tweets-left {
	width: 80px;
}

.tweets-left svg {
	display: block;
	width: inherit;
	height: 3rem;
}

.tweets-left svg line {
	stroke: rgb(154, 154, 154);
}

.tweets-right {
	width: 700px;
}

.avatars {
	border-radius: 50%;
	width: 3rem;
	height: 3rem;
	margin: .5rem 1rem;
}

.tweets-right strong {
	line-height: 2.5rem;
}

.tweets-right strong+span {
	margin-left: .5rem;
	color: rgb(117, 117, 117);
	/*font-size: .95rem;*/
}

.tweets-right p {
	margin-top: 0;
	white-space: pre-line; /* Perfect solution to solve the line-break issue! */
}

.icon-bottom {
	display: flex;
	justify-content: space-around;
}

.icon-comment,
.icon-like {
	display: inline-block;
}

.icon-bottom svg {
	height: 2.5rem;
	width: 2.5rem;
	fill: rgb(91, 112, 131);
	vertical-align: middle;
	margin-right: .5rem;
	border-radius: 50%;
}

.icon-bottom svg:hover {
	cursor: pointer;
	fill: rgb(74, 161, 235) !important;
	background-color: rgb(237, 245, 253);
}

.icon-like svg:hover {
	fill: rgb(206, 59, 96) !important;
	background-color: rgba(224, 36, 94, 0.1);
}

.icon-bottom path {
	transform: translate(8px, 8px);
}

.icon-bottom span {
	vertical-align: middle;
	color: rgb(91, 112, 131);
	display: inline-block;
	width: 2px;
}

@media (max-width: 600px) {
	.tweets-right {
		margin-right: 5%;
	}
}

@media (max-width: 400px) {
	.avatars {
		width: 2.5rem;
		height: 2.5rem;
		margin: .5rem .5rem;
	}

	.tweets-right strong+span {
		font-size: .9rem;
	}

}


