.tweet-area {
	display: flex;
	width: 100%;
}

.tweet-left {
	width: 80px;
}

.tweet-right {
	width: 700px;
}

.avatar {
	border-radius: 50%;
	width: 3rem;
	height: 3rem;
	margin: 1.5rem 1rem;
}

.tweet-right form textarea {
	border: none;
	padding: 1rem .5rem;
	padding-left: 0;
	width: 100%;
	margin: 1.5rem auto;
	font-size: 1.1rem;
	font-family: 'Roboto', -apple-system, sans-serif;
	line-height: 1.4rem;
	resize: none;
	overflow: auto;
}

.tweet-right form textarea:focus {
	outline: none;
}

.tweet-right form hr {
	stroke: rgb(236, 238, 240);
}

.tweet-right .bottomButtons {
	display: flex;
	justify-content: space-between;
}

.tweet-right form svg {
	height: 2.5rem;
	width: 2.5rem;
	fill: rgb(74, 161, 235);
	border-radius: 50%;
	display: inline-block;
}

.tweet-right form svg:hover {
	background-color: rgb(237, 245, 253);
	cursor: pointer;
}

.tweet-right form svg g {
	transform: translate(8px, 8px);
}

.tweet-right form button {
	display: inline-block;
	width: 15%;
	min-width: 4.5rem;
	margin: 0;
	border-radius: 20px;
	font-weight: 700;
	-webkit-filter: none;
	filter: none;
	cursor: pointer;
}

.tweet-right form button:focus {
	outline: none;
}

.tweet-right form button:disabled {
	background-color: rgb(165, 208, 245);
	cursor: initial;
}

.tweet-right span.picker {
	position: absolute;
}

@media (max-width: 992px) {
	.tweet-main {
		margin-top: 50px;
	}
}

@media (max-width: 600px) {
	.tweet-main {
		width: 100%;
	}

	.tweet-right {
		margin-right: 5%;
	}
}

@media (max-width: 400px) {
	.avatar {
		width: 2.5rem;
		height: 2.5rem;
		margin: 1rem .5rem;
	}

	.tweet-right form textarea {
		margin: .6rem auto 1.5rem auto;
		font-size: 1rem;
	}

	.tweet-right form button {
		min-width: 3.5rem;
		padding: 0;
	}
}

















