.container {
	width: 70%;
	min-height: 100vh;
	margin: auto;
	display: flex;
}

.main-left {
	height: inherit;
	border-right: 1px solid rgb(236, 238, 240);
	width: 30%;
}

nav {
	position: fixed;
	width: 21%;
}

nav ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

nav img {
	width: 3.5rem;
	height: 3.5rem;
	margin-left: 2.5rem;
}

nav a {
	margin-bottom: 1rem;
	text-decoration: none;
	display: block;
	color: #000;
}

nav svg {
	height: 1.5rem;
	width: 1.5rem;
	margin-right: 1rem;
	vertical-align: top;
}

nav li {
	padding: .5rem; 
}

nav h2 {
	display: inline-block;
	font-size: 1.2rem;
	margin: 0;
}

nav a:hover {
	width: 50%;
	min-width: 8rem;
	border-radius: 20px;
	background-color: rgb(237, 245, 253);
  color: rgb(74, 161, 235);
  background-clip: content-box;
  fill: rgb(74, 161, 235);
}

nav a.active {
	color: rgb(74, 161, 235);
	fill: rgb(74, 161, 235);
}

.main-right {
	width: 70%;
	height: inherit;
}

.main-right .horizontalNav {
	display: none;
}

@media (min-width: 1400px) {
  .container {
    width: 65%;
  }
}

@media (max-width: 1200px) {
	.container {
		width: 75%;
	}
}

@media (max-width: 992px) {
	.container {
		width: 70%;
	}

	.main-left {
		display: none;
	}
	.main-right {
		width: 100%;
		margin: 0 auto;
	}
	.main-right .horizontalNav {
		display: block;
		overflow-x: hidden;
	}
}

@media (max-width: 768px) {
	.container {
		width: 85%;
	}
}

@media (max-width: 600px) {
	.container {
		width: 100%;
	}
}


