div.clearfix {
	display: flex;
	align-items: center;
}

img[alt="nickname"],
img[alt="email"] {
	width: 1.5rem;
	height: 1.5rem;
}

.clearfix span {
	margin-left: 1rem;
}

.clearfix span+span {
	display: inline-block;
	padding: .5rem;
	margin-left: auto;
	height: 36.8px;
}

.clearfix span+span:hover {
	background-color: rgb(237, 245, 253);
	border-radius: 50%;
}

.clearfix img[alt="edit"] {
	width: 1.3rem;
	height: 1.3rem;
	cursor: pointer;
}

.clearfix input {
	margin-left: 1rem;
	font-size: 1rem;
	height: 3rem;
	padding: 0 1rem;
	/*vertical-align: middle;*/
	border-radius: 4px;
	border: 1px solid rgba(0, 0, 0, 0.23);
}

.clearfix input:hover {
	border-color: rgba(0, 0, 0, 0.87);
}

.clearfix input:active {
	border-color: rgb(53, 118, 203);
}

img[alt="close"],
img[alt="confirm"] {
	width: 1.1rem;
	height: 1.1rem;
	/*vertical-align: middle;*/
	margin: 0 .3rem;
	cursor: pointer;
}

@media (max-width: 330px) {
	.clearfix input {
		width: 11rem;
	}
}







