.form-wrapper {
	width: 100%;
}

.logo-signin {
	width: 2.5rem;
	height: 2.5rem;
	margin-top: 6rem;
	background-color: #DC004E;
	border-radius: 50%;
}

.icon-lock {
	fill: #FFFFFF;
	transform: translate(8px, 8px);
}

h1 {
	font-size: 1.5rem;
	font-weight: 400;
	margin-top: .5rem;
	margin-bottom: 2rem;
}

.form-signin {
	width: 30vw;
	min-width: 18rem;
	margin: 0 auto;
}

form input {
	display: block;
	width: 100%;
	margin: 1.5rem auto;
	padding: 1rem .5rem;
	border-radius: 4px;
	border: 1px solid rgba(0, 0, 0, 0.23);
	font-size: 1rem;
}

form input:hover {
	border-color: rgba(0, 0, 0, 0.87);
}

form input:active {
	border-color: rgb(53, 118, 203);
}

.form-signin p {
	font-size: 15px;
	color: red;
}

form button {
	width: 100%;
	margin: 1.5rem auto;
	padding: .6rem 0;
	font-size: .9rem;
	font-weight: 500;
	color: rgb(254, 254, 254);
	border-radius: 4px;
	border: none;
	background-color: rgb(53, 118, 203);
	-webkit-filter: drop-shadow(1px 1px 1px #666);
	filter: drop-shadow(1px 1px 1px #666);
	cursor: pointer;
}

form button:hover {
	background-color: rgb(36, 83, 142);
}








